.banner img{
  animation: updown 3s linear infinite;
  width: 400px;
  height: 400px;
}
@keyframes updown {
  0% {
    transform: translateY(-20px);

  }
  50%{
    transform: translateY(20px);
  }
  100%{
    transform: translateY(-20px);
  }
}

